@import 'style/color.css';
@import 'style/font.css';
@import 'style/global.css';

.App {
    min-height: 100dvh;
    display: flex;
    flex-direction: column;

    .main-main {
        padding: 20px 16px;
        display: flex;
        flex-direction: column;
        gap: 30px;

        .pc-build {
            display: flex;
            flex-direction: column;
            gap: 10px;

            &-own {
                display: flex;
                flex-direction: column;
                gap: 10px;
                box-shadow: rgba(30, 31, 33, 0.15) 0 2px 30px;
                background-color: #f8f8f6;
                border-radius: 2px;
                padding: 20px;

                &-header {
                    font-size: 1.2rem;
                    font-weight: 600;
                }

                &-button {
                    cursor: pointer;
                    user-select: none;
                    text-align: center;
                    font-weight: 500;
                    padding: 6px 0;
                    border: 1px solid var(--main-color);
                    border-radius: 8px;
                    font-size: 0.9rem;
                    color: var(--main-color);
                }
            }

            &-by-users {
                box-shadow: rgba(30, 31, 33, 0.15) 0 5px 30px;
                background-color: #f8f8f6;
                border-radius: 2px;
                padding: 20px;

                &-header {
                    font-size: 1.2rem;
                    font-weight: 600;
                }
            }
        }
    }
}
