.side-menu-footer {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    border: 0;

    &-ask-button {
        cursor: pointer;
        user-select: none;
        text-align: center;
        font-weight: 500;
        padding: 8px 0;
        border: 1px solid rgba(0, 0, 0, 0.4);
        border-radius: 8px;
    }

    &-separator {
        border-bottom: 1px dotted rgba(0, 0, 0, 0.2);
    }

    &-social {
        //Запланировано сделать max-width у всего side-menu = 425px
        //width: 100%;
        //max-width: 400px;
        margin: 0 auto;
        flex: 1 1 fit-content;
        display: flex;
        justify-content: space-between;
        align-items: center;


        &-item {
            box-sizing: content-box;
            height: 32px;
            width: 32px;
            cursor: pointer;
            user-select: none;
            padding: 8px;
            fill: color-mix(in srgb, var(--main-color) 15%, rgba(0, 0, 0, 0.4));
        }
    }
}